/* Blog Post Styles */

.blog-post-container {
  max-width: 900px;
  margin: 2rem auto; /* Add top margin */
  padding: 2rem;
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow-sm);
}

.blog-post-navigation {
  margin-bottom: 1.5rem;
}

.blog-back-button {
  display: inline-flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: var(--color-background-light);
  color: var(--color-text);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-sm);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.blog-back-button:hover {
  background-color: var(--color-background);
  color: var(--color-primary);
}

.blog-post {
  margin-bottom: 2rem;
}

.blog-post-category {
  margin-bottom: 1rem;
}

.blog-post-category-label {
  display: inline-block;
  padding: 0.3rem 0.8rem;
  background-color: var(--color-primary);
  color: var(--color-white);
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05rem;
  border-radius: var(--border-radius-sm);
}

.blog-post-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  line-height: 1.2;
  color: var(--color-heading);
}

.blog-post-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  color: var(--color-text-medium);
}

.blog-post-author {
  font-weight: 500;
}

.blog-post-featured-image {
  margin-bottom: 2rem;
  border-radius: var(--border-radius);
  overflow: hidden;
}

.blog-post-featured-image img {
  width: 100%;
  height: auto;
  display: block;
}

.blog-post-content {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--color-text);
  margin-bottom: 2rem;
}

.blog-post-content p {
  margin-bottom: 1.5rem;
}

.blog-post-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 2rem;
  align-items: center;
}

.blog-post-tags-label {
  font-weight: 600;
  margin-right: 0.5rem;
}

.blog-post-tag {
  padding: 0.3rem 0.7rem;
  background-color: var(--color-background-light);
  border-radius: var(--border-radius-sm);
  font-size: 0.8rem;
  transition: all 0.2s ease;
}

.blog-post-tag:hover {
  background-color: var(--color-primary-light);
  color: var(--color-primary);
}

.blog-post-share {
  display: flex;
  align-items: center;
  padding: 1.5rem 0;
  border-top: 1px solid var(--color-border-light);
  border-bottom: 1px solid var(--color-border-light);
  margin-bottom: 2rem;
}

.blog-post-share-label {
  margin-right: 1rem;
  font-weight: 500;
}

.blog-post-social-links {
  display: flex;
  gap: 1rem;
}

.blog-post-social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--color-background-light);
  color: var(--color-text-medium);
  transition: all 0.2s ease;
}

.blog-post-social-link:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
}

.blog-post-comments {
  margin-top: 3rem;
}

.blog-post-comments-heading {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--color-heading);
}

.blog-post-comment {
  padding: 1.5rem;
  background-color: var(--color-background-light);
  border-radius: var(--border-radius);
  margin-bottom: 1rem;
}

.blog-post-comment-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.8rem;
}

.blog-post-comment-author {
  font-weight: 600;
}

.blog-post-comment-date {
  font-size: 0.85rem;
  color: var(--color-text-medium);
}

.blog-post-not-found {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--color-text);
}

.blog-post-not-found h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: var(--color-heading);
}

.blog-post-not-found p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  color: var(--color-text-medium);
}

/* Responsive styles */
@media screen and (max-width: 767px) {
  .blog-post-container {
    padding: 1.5rem;
  }
  
  .blog-post-title {
    font-size: 1.8rem;
  }
  
  .blog-post-meta {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .blog-post-content {
    font-size: 1rem;
  }
  
  .blog-post-share {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .blog-post-title {
    font-size: 2.2rem;
  }
}